.App {
  text-align: center;
}
.App input, 
.App select, 
.navbar{
  width: 300px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.App input, 
.App select{
  height: 30px;
}

.nav-item{
  font-size: 16px;
}

.form-item{
  width: 300px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 15px;
}

.form-item .form-label{
  float: left;
  margin-left: 10px;
  text-align: left;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
